/**
 * Cookie Banner
 * --------------------------------------------------
 */

.cookie-overlay,
.cookie-jar {
  font-family:      'Source Sans Pro', sans-serif;
  background-color: @cookie-bg;
  color:            @cookie-fg;
  padding:          1em 0;
}

.cookie-overlay {
  display:          flex;
  align-items:      center;
  min-width:        480px;
  min-height:       270px;
}
@media (min-width: @screen-md-min) {
  .cookie-overlay {
    min-width:      592px;
    min-height:     333px;
  }
}
@media screen and (max-width: @screen-xs-max) {
  .cookie-overlay .embed-responsive-item {
    min-width:      auto;
    min-height:     auto;
  }
}

.cookie-heading {
  font-size:        1.35em;
  font-weight:      300;
  margin-top:       0;
  color:            @cookie-fg;
}

.cookie-link,
.cookie-link:active,
.cookie-link:focus,
.cookie-link:hover,
.cookie-link:visited {
  color:            @cookie-link;
}

.checkbox-cookie-setting {
  position:         relative;
  margin-bottom:    10px;
  label {
    padding-left:   1.5rem;
    display:        inline-block;
  }
  &+.checkbox-cookie-setting {
    margin-top: -5px;
  }
  &:last-child {
    margin-bottom: 0px;
  }
  &:first-child {
    margin-top: 0px;
  }
}
.checkbox.checkbox-cookie-setting input[type=checkbox] {
  margin-left:      0;
  position:         absolute;
}

.cookie-actions {
  list-style-type:  none;
  margin-top:       1rem;
  margin-bottom:    0;
  padding-left:     1.5rem;
}

.cookie-actions > li {
  margin-bottom:    0.4rem;
  margin-right:     0.4rem;
  display:          inline-block;
}

@media (min-width: @screen-md-min) {
  .cookie-actions {
    margin-top:     0;
  }
  .cookie-actions > li {
    display:        block;
  }
}

.cookie-btn-overlay {
  margin-top:       1rem;
}

.cookie-lid {
  background-color: @cookie-bg;
  color:            @cookie-fg;
  padding:          0.5em 0.5em;
  text-align:       left;
  position:         fixed;
  left:             0;
  bottom:           0;
  z-index:          9999;
}
.cookie-btn.cookie-btn {
  font-family:      'Source Sans Pro', sans-serif;
}

@media print {
  .cookie-overlay,
  .cookie-jar,
  .cookie-lid {
    display:        none;
  }
}

.cookie-btn {
  display:          inline-block;
  position:         relative;
  text-align:       center;
  white-space:      nowrap;
  vertical-align:   middle;
  user-select:      none;
  border:           1px solid transparent;
  padding:          .5rem 1rem;
  border-radius:    .275rem;
  transition:       color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  cursor:           pointer;
  box-shadow:       inset 0 1px 0 rgba(100, 100, 100, .15), 0 1px 1px rgba(0, 0, 0, .075);
  font-size:        1rem;
  text-decoration:  none;
}
.cookie-btn:active,
.cookie-btn:focus,
.cookie-btn:hover,
.cookie-btn:visited {
  text-decoration:  underline;
}

.cookie-btn:focus {
  outline:          0;
  box-shadow:       0 0 0 .2rem rgba(25, 181, 254, .5);
}
.cookie-btn:active {
  box-shadow:       inset 0 3px 5px rgba(0, 0, 0, .25);
}
.cookie-btn:focus:active {
  box-shadow:       0 0 0 .2rem rgba(25, 181, 254, .5), inset 0 3px 5px rgba(0, 0, 0, .25);
}
@media screen and (prefers-reduced-motion: reduce) {
  .cookie-btn {
    transition:     none;
  }
}

.cookie-btn-primary {
  color:            @cookie-btn1-fg;
  background-color: @cookie-btn1-bg;
  border-color:     @cookie-btn1-bd;
}
.cookie-btn-primary:hover,
.cookie-btn-primary:focus,
.cookie-btn-primary:active {
  color:            @cookie-btn1-fg-hover;
  background-color: @cookie-btn1-bg-hover;
  border-color:     @cookie-btn1-bd-hover;
}
.cookie-btn-primary:hover:active,
.cookie-btn-primary:focus:active {
  background-color: @cookie-btn1-bg-active;
  border-color:     @cookie-btn1-bd-active;
}

.cookie-btn-secondary {
  color:            @cookie-btn2-fg;
  background-color: @cookie-btn2-bg;
  border-color:     @cookie-btn2-bd;
}
.cookie-btn-secondary:hover,
.cookie-btn-secondary:focus,
.cookie-btn-secondary:active {
  color:            @cookie-btn2-fg-hover;
  background-color: @cookie-btn2-bg-hover;
  border-color:     @cookie-btn2-bd-hover;
}
.cookie-btn-secondary:hover:active,
.cookie-btn-secondary:focus:active {
  background-color: @cookie-btn2-bg-active;
  border-color:     @cookie-btn2-bd-active;
}

.cookie-btn-sm {
  padding:          .25rem .5rem;
  font-size:        .875rem;
  line-height:      1.5;
  border-radius:    .25rem;
}
body {
  padding-bottom:   3rem;
}

.cookie-container {
  display:          block;
  padding-right:    floor((@grid-gutter-width / 2));
  padding-left:     floor((@grid-gutter-width / 2));
  margin-right:     auto;
  margin-left:      auto;
}
@media screen and (min-width: @screen-sm-min) {
  .cookie-container {
    max-width:      90rem;
    padding-left:   @grid-gutter-width;
    padding-right:  @grid-gutter-width;
  }
}

.cookie-row {
  display:          flex;
  flex-wrap:        wrap;
  margin-right:     -floor((@grid-gutter-width / 2));
  margin-left:      -floor((@grid-gutter-width / 2));
}
.cookie-col-double,
.cookie-col-single {
  position:         relative;
  width:            100%;
  /* Prevent columns from collapsing when empty */
  min-height:       1px;
  padding-right:    floor((@grid-gutter-width / 2));
  padding-left:     floor((@grid-gutter-width / 2));
}
@media screen and (min-width: @screen-md-min) {
  .cookie-col-double {
    flex-basis:     0;
    flex-grow:      2;
    /*Add a `max-width` to ensure content within each column does not blow out*/
    /*the width of the column. Applies to IE10+ and Firefox. Chrome and Safari*/
    /*do not appear to require this.*/
    max-width:      100%;
  }
  .cookie-col-single {
    flex-basis:     0;
    flex-grow:      1;
    max-width:      100%;
  }
}
